body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

hr {
  color: gainsboro;
  background-color: gainsboro;
  height: 1px;
}

.App {
  text-align: center;
  margin: auto;
  background-color: #001f3f;
}

.Section {
  width: 40%;
  border: 2px solid gainsboro;
  border-radius: 5px;
  background-color: white;
  text-align: left;
  margin: 5px auto;
  padding: 10px;
  min-width: 450px; /* todo content based*/
}

.Header {
  text-align: center;
  font-size: 135%;
  margin: 5px auto;
  display: -webkit-flex;
  display: flex;
}

.Header .Left {
  -webkit-flex: 1 1;
          flex: 1 1;
}

.Header .Right {
  color: gainsboro;
  width: auto;
}

.item {
  border: 2px solid #AAAAAA;
  background-color: #DDDDDD;
  margin: 1px;
  display: -webkit-flex;
  display: flex;
}

.item .Left {
  -webkit-flex: 1 1;
          flex: 1 1;
}

.item .Right {
  color: gainsboro;
  width: auto;
}

